// .p-wysiwyg__editor will get replaced by Redactor
.p-wysiwyg__editor {
    height: 0;
    padding: 0;
    margin: 0;
    opacity: 0;
    overflow: hidden;
}
.p-wysiwyg__output {
    height: 0;
    overflow: hidden;
    opacity: 0;
    border: 0px !important;
    position:absolute;
}
